.footer{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #222;
    color: beige;
    outline: 1px solid;
}

.socialIcons{
    display: flex;
    margin: 15px;

}
.socialIcons li{
    display: block;
    width: 50px;
    height: 50px;
    background-color: #b46e3c;
    transform: rotate(45deg);
    margin: 15px;

}
